html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: Inter, sans-serif;
  margin: 0;
  padding: 0;
  background-color: var(--background-color);
  color: var(--text-color);
}

:root h1 {
  font-weight: 500;
  margin: 0px;
}
:root h2 {
  font-size: 2.8rem;
  text-align: center;
  margin-bottom: 59px;
}
:root h3 {
  font-size: 1.68rem;
  font-weight: 800;
  margin: 0px;
}
:root h4 {
  font-weight: 600;
  font-size: 2rem;
  text-align: center;
}
:root p {
  font-weight: 500;
  line-height: 24px;
  margin: 0px;
}
:root a {
  cursor: pointer;
}

@media (min-width: 20em) {
  h1 {
    font-size: 1.75rem;
  }
}
:root {
  --white: rgb(255, 255, 255);
  --black: hsl(0, 0%, 12%);
  --brown: hsl(29, 30%, 61%);
  --light-brown: hsl(29, 63%, 82%);
  --sage: hsl(138, 21%, 88%);
  --light-yellow: hsl(40, 43%, 96%);
  --background-color: var(--white);
  --text-color: var(--black);
  --buttons: var(--white);
  --buttons-hover: var(--black);
}

@keyframes fadeIn {
  from {
    top: 0;
  }
  to {
    top: -10px;
  }
}
@keyframes wobble {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0px);
  }
}
.button {
  display: inline-block;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
  border-radius: 37px;
  background-color: var(--buttons);
  font-weight: 800;
  font-size: 1.25rem;
  padding: 14px 20px 14px 20px;
  transition: 0.5s;
}
.button:hover {
  position: relative;
  top: -10px;
  animation: fadeIn 0.5s;
  background-color: var(--buttons-hover);
  color: var(--white);
}

.socials {
  display: flex;
  gap: 14px;
}

body {
  background-image: url(/public/assets/background.png);
  background-repeat: no-repeat;
  background-position: top;
  background-position-y: 60px;
}

header {
  display: flex;
  align-items: center;
  height: 73px;
  background-color: var(--white);
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
  position: sticky;
  top: 0;
}
header nav {
  padding: 0px 20px 0px 20px;
}

svg {
  fill: var(--black);
  transition: 0.4s;
}
svg:hover {
  fill: var(--light-brown);
}

main {
  display: flex;
  flex-direction: column;
  margin: auto;
  padding: 40px 0px 120px 0px;
  gap: 133px;
  max-width: 302px;
}
main .project-container {
  display: flex;
  flex-direction: column;
  gap: 108px;
}
main .project-container .project > * {
  margin-top: 25px;
}

.project-img {
  width: 100%;
  border-radius: 19px;
}

.skills {
  display: flex;
  justify-content: center;
  gap: 30px;
}
.skills .icon-text {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  width: 81px;
}
.skills .icon-text .skill-icon {
  width: 53px;
}
.skills span {
  font-weight: 600;
  text-align: center;
}

.hero-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
}
.hero-container .profile-img {
  width: 272px;
}
.hero-container .hero-text {
  display: flex;
  flex-direction: column;
  gap: 23px;
}
.hero-container .hero-text .highlight {
  font-weight: 900;
}
.hero-container .arrow-img {
  width: 50px;
  animation: wobble 1.7s;
  animation-iteration-count: infinite;
  animation-timing-function: ease;
}

.cite-container {
  text-align: center;
}
.cite-container .fancy-dots {
  display: inline-block;
  font-style: normal;
  font-size: 5rem;
  color: var(--light-brown);
  padding-bottom: 56px;
  line-height: 0.8;
}
.cite-container p {
  font-style: italic;
  padding-bottom: 56px;
  font-weight: 500;
}
.cite-container p span {
  font-weight: 300;
}

footer {
  background-color: var(--white);
  box-shadow: 4px 0px 10px rgba(0, 0, 0, 0.25);
  padding: 33px 0px 33px 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32px;
}